import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import NameList from './NameList';
import Cookies from 'js-cookie';
import { database, ref, onValue, set } from './firebaseConfig';

const AppContainer = styled.div`
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
`;

const Title = styled.h1`
  font-size: 2rem;
  margin-bottom: 20px;
`;

const ResetButton = styled.button`
  position: absolute;
  bottom: 10px;
  right: 10px;
  background: transparent;
  border: none;
  color: #aaa;
  font-size: 0.8rem;
  cursor: pointer;
`;

export interface Name {
  id: number;
  name: string;
  votes: number;
  comments: Record<number, string>;
}

const App: React.FC = () => {
  const [names, setNames] = useState<Name[]>([]);

  useEffect(() => {
    const namesRef = ref(database, 'names');
    const unsubscribe = onValue(namesRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const formattedNames = Object.keys(data).map(key => ({
          id: Number(key),
          ...data[key],
        }));
        setNames(formattedNames);
      }
    });

    return () => unsubscribe();
  }, []);

  const handleReset = async () => {
    const initialNames = [
      { id: 0, name: 'Aďo B.', votes: 0, comments: {} },
      { id: 1, name: 'Anka M.', votes: 0, comments: {} },
      { id: 2, name: 'Daniel R.', votes: 0, comments: {} },
      { id: 3, name: 'Ivan O.', votes: 0, comments: {} },
      { id: 4, name: 'Kika K.', votes: 0, comments: {} },
      { id: 5, name: 'Lenka N.', votes: 0, comments: {} },
      { id: 6, name: 'Martin N.', votes: 0, comments: {} },
      { id: 7, name: 'Samo S.', votes: 0, comments: {} },
      { id: 8, name: 'Tomas V.', votes: 0, comments: {} },
      { id: 9, name: 'Vlado K.', votes: 0, comments: {} },
    ];

    await Promise.all(initialNames.map((name) => 
      set(ref(database, `names/${name.id}`), name)
    ));
    Cookies.remove('votedName');
  };

  return (
    <AppContainer>
      <Title>ISTC Sprint Hero</Title>
      <NameList names={names} setNames={setNames} />
      <ResetButton onClick={handleReset}>Reset</ResetButton>
    </AppContainer>
  );
}

export default App;
